
import { useState } from "react";
import React from "react";
import { withTranslation } from "react-i18next";

const CompoundInterest = (props) => {
    const [principal, setPrincipal] = useState();
    const [years, setYears] = useState();
    const [interest, setInterest] = useState();
    const [time, setTime] = useState();
    const [mvalue, setResult] = useState();


    const calculate = () => {
        // A=p(1+(r/n))^(nt)
        const result = parseInt(principal * Math.pow((1 + (interest / (years * 100))), (time * years)));

        setResult(result.toLocaleString('en-IN', {
            maximumFractionDigits: 0,
            currency: 'INR'
          }));
    };

    const{t}=props;

    return (
        <div className="row">
            <div className="col-lg-8">
                <form>
                    <div className="mb-3 row">
                        <label htmlFor="exampleFormControlInput1" className=" col-sm-4 calc-label">{t("CTAFooter:calculator4")}</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setPrincipal(e.target.value)} type="text" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label htmlFor="exampleFormControlInput1" className="col-sm-4 calc-label">{t("CTAFooter:calculator5")}</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setInterest(e.target.value)} type="text" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label htmlFor="exampleFormControlInput1" className=" col-sm-4 calc-label">{t("CTAFooter:calculator6")}</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setTime(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <div className="mb-3 row">
                        <label htmlFor="exampleFormControlInput1" className=" col-sm-4 calc-label">Compounding Period in Years</label>
                        <div className="col-sm-8">
                            <input onChange={(e) => setYears(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                        </div>
                    </div>
                    <button onClick={() => { calculate(); }} type="button" className="btn btn-primary">Calculate</button>
                </form>
            </div>
            <div className="col-lg-4">
                <div className="pricipalDiv">
                    <h3>{t("CTAFooter:calculator1")}</h3>
                    <h4>Maturity Value: ₹ {mvalue}</h4>

                </div>
            </div>
        </div>

    )
}

export default withTranslation()(CompoundInterest)